.main-navigation {
  display: inline-block;
  width: 75%;
  height: auto; }

#site-navigation ul {
  display: inline-block;
  float: right;
  list-style-type: none;
  padding: 0;
  margin: 0; }

#site-navigation ul li {
  display: inline-block;
  float: left;
  padding-left: 24px; }

#site-navigation ul li a {
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 0.5px; }

#site-navigation ul li.current-menu-item a {
  color: green; }

/* Changing Susy default global settings */
/* Makes border-box properties */
*, *:before, *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

.site-wrapper {
  background: url("../images/bg-baseline.png") repeat top left;
  width: 100%; }
  @media (max-width: 767px) {
    .site-wrapper {
      background: green; } }
  @media (min-width: 768px) {
    .site-wrapper {
      background: yellow; } }
  @media (min-width: 1280px) {
    .site-wrapper {
      background: orange; } }
  @media (min-width: 1366px) {
    .site-wrapper {
      background: red; } }

.site-header,
.site-content,
.site-footer {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  margin: 0 auto;
  overflow: hidden;
  background: white;
  padding: 32px 16px; }
  .site-header:after,
  .site-content:after,
  .site-footer:after {
    content: " ";
    display: block;
    clear: both; }

.site-branding {
  width: 32.2%;
  float: left;
  margin-right: 1.69%; }

#site-navigation {
  width: 66.1%;
  float: right;
  margin-right: 0; }

.site-footer {
  background: black; }

.content-area {
  width: 66.1%;
  float: left;
  margin-right: 1.69%; }

.widget-area {
  width: 32.2%;
  float: right;
  margin-right: 0; }

.site-banner {
  width: 100%;
  height: 450px;
  background: url("../images/banner-023.jpg") no-repeat center center;
  background-size: cover; }

.site-banner.sticky {
  height: 100px;
  -webkit-transition: all 0.8s ease;
  transition: all 0.8s ease; }

.site-banner .inner-wrapper h3,
.site-banner .inner-wrapper h1 {
  display: none; }

h1.site-title {
  font-size: 27px;
  line-height: 30px;
  margin: 0; }

p.site-description {
  font-size: 12px;
  line-height: 18px;
  margin: 0; }

@media print {
  /* ==========================================================================
   Print styles.
   Inlined to avoid the additional HTTP request: h5bp.com/r
   ========================================================================== */
